import Vue from 'vue'
import Router from 'vue-router'
import Texteaccueil from '@/components/Texte.vue'
import TexteFormulaire from '@/components/Formulaire.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Texteaccueil',
      component: Texteaccueil
    },
    {
      path: '/Formulaire',
      name: 'TexteFormualire',
      component: TexteFormulaire
    }
  ]
})



